/**********************
* Base style
**********************/
html,
body {
  height: 100%;
}

body {
  background: rgb(33, 142, 117);
  font-family: 'Caveat', cursive;
  font-size: 10px;
  color: #000;
  font-weight: 400;
  min-width: 320px;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  position: relative;
}

ul {
  list-style: none;
  padding: 0;
}

br {
  font-size: 0;
}

* {
  box-sizing: border-box;
}
