/**********************
* Base style
**********************/
html,
body {
  height: 100%;
}

body {
  background: #218e75;
  font-family: 'Caveat', cursive;
  font-size: 10px;
  color: #000;
  font-weight: 400;
  min-width: 320px;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  position: relative;
}

ul {
  list-style: none;
  padding: 0;
}

br {
  font-size: 0;
}

* {
  box-sizing: border-box;
}

/**********************
* typography
**********************/
h1,
h2,
h3,
h4,
h5,
h6,
p,
hr {
  margin: 0;
  font-weight: 400;
}

a {
  display: inline-block;
}

.main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  font-size: 72px;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 30px;
  font-weight: 700;
  color: #d1e900;
}

.grid {
  border-collapse: collapse;
  margin-bottom: 50px;
  border-radius: 10px;
  overflow: hidden;
  background-color: #f6f8eb;
}

.elem {
  width: 100px;
  height: 100px;
  cursor: pointer;
  text-align: center;
  font-size: 72px;
  color: #555555;
}

.elem:nth-of-type(2) {
  border-left: 5px solid #218e75;
  border-right: 5px solid #218e75;
}

.row:nth-of-type(2) {
  border-top: 5px solid #218e75;
  border-bottom: 5px solid #218e75;
}

.btn {
  width: 250px;
  font-size: 36px;
  line-height: 50px;
  cursor: pointer;
  text-transform: uppercase;
  border-radius: 30px;
  border: none;
  outline: none;
  transition: .3s;
  font-family: 'Caveat', cursive;
  font-weight: 700;
  color: #555555;
  background-color: #f6f8eb;
  letter-spacing: .5px;
}

.btn:hover, .btn.hover {
  background-color: #d1e900;
  letter-spacing: 3px;
}

.btn.hover {
  animation: win 1.4s infinite;
}

.win {
  animation: win 1.4s infinite;
}

@keyframes win {
  0% {
    background-color: #f6f8eb;
  }
  50% {
    background-color: #d1e900;
  }
  100% {
    background-color: #f6f8eb;
  }
}

